import * as React from "react"

const TagsList = ({ tags, barBefore = false }) => {
  const hasTags = tags && tags.length > 0;
  if (!hasTags) return;
  return (
    <>
      {barBefore && '|'}
      {(tags ?? []).map(tag => <span className="tag" key={tag}>#{tag.toUpperCase()}</span>)}
    </>
  )
}

export default TagsList;
